%;\n  box-sizing: border-box;\n  padding: 0 0;\n  margin: 0 0;\n  color: #000;\n  font-size: 17px;\n  text-transform: uppercase;\n  font-weight: 500;\n  line-height: 20px;\n}\n.listing h3 i {\n  margin-left: 5px;\n}\n.listing:after {\n  content: \"\";\n  position: absolute;\n  bottom: -8px;\n  right: -20px;\n  width: 60px;\n  height: 60px;\n  background-size: cover;\n  background-repeat: no-repeat;\n  background-position: top;\n  background-image: url('icon4.png');\n}\n/*------ home css stop -----*/\nbutton.nav_bar {\n  content: \"\";\n  position: absolute;\n  left: 30px;\n  top: 30px;\n  font-size: 28px;\n  background: transparent;\n  border: none;\n  outline: none;\n  color: #fff;\n}\n/*------ home css stop -----*/\n/*# sourceMappingURL=data:application/json;base64, */";